@import 'palette';
@import 'normalize.css';
* {
  box-sizing: border-box;
}
html, body, .main-content {
  height: 100%;
}
body {
  padding: 0;
  margin: 0;
  font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 1.6;
  color: $cadet-gray;
  background-color: $indigo-dye;
}

a:link {
  color: $celestial-blue;
  text-decoration: none;
}
a:visited {
  color: darken($celestial-blue, 15%);
}
a:hover,
a:active {
  color: lighten($celestial-blue, 15%);
}

.page-header {
  box-sizing: border-box;
  text-align: center;
}

.project-name {
  margin-top: 0;
  margin-bottom: 0.1rem;

  @media screen and (min-width:64em) {
    font-size: 3.25rem;
  }
  @media screen and (min-width:42em) and (max-width:64em) {
    font-size: 2.25rem;
  }
  @media screen and (max-width:42em) {
    font-size: 1.75rem;
  }
}
a.header-link:link,
a.header-link:visited {
  color: #666;
  text-decoration: none;
  transition: 0.4s;
}
a.header-link:hover,
a.header-link:active {
  text-shadow: 1px 1px 8px #93c9bc;
  transition: 0.3s;
  text-decoration: none;
}

.project-tagline {
  margin-bottom: 2rem;
  font-weight: normal;
  opacity: 0.7;
  @media screen and (min-width:64em) {
    font-size: 1.25rem;
  }
  @media screen and (min-width:42em) and (max-width:64em) {
    font-size: 1.15rem;
  }
  @media screen and (max-width:42em) {
    font-size: 1rem;
  }
}

.main-content {
  position: relative;

  @media screen and (min-width:64em) {
    max-width: 64rem;
    padding: 1.25rem 6rem 0.5rem;
    margin: 0 auto;
    font-size: 1.15rem;
  }
  @media screen and (min-width:42em) and (max-width:64em) {
    padding: 1.15rem 4rem 0.5rem;
    font-size: 1.1rem;
  }
  @media screen and (max-width:42em) {
    padding: 1rem 0.5rem 0.5rem;
    font-size: 1rem;
  }
}

.main-content:first-child {
  margin-top: 0;
}
.main-content img {
  max-width: 100%;
}
.main-content h1, .main-content h2, .main-content h3, .main-content h4, .main-content h5, .main-content h6 {
  margin-bottom: 1rem;
  font-weight: normal;
  color: #5F7585;

  @media screen and (min-width:64em) {
    margin-top: 2rem;
  }
  @media screen and (max-width:64em) {
    margin-top: 0.25rem;
  }
}

.main-content .logo {
  position: absolute;
  height: 200px;
  width: 200px;
  top: 30%;
  left: 50%;

  margin-top: -100px;
  margin-left: -100px;
}
.main-content .logo img {
  height: auto;
  max-width: 100%;
  width: 200px;
}

.main-content p {
  margin-bottom: 1em;
}
.main-content code {
  padding: 2px 4px;
  font-family: Consolas, "Liberation Mono", Menlo, Courier, monospace;
  font-size: 0.9rem;
  color: #383e41;
  background-color: #f3f6fa;
  border-radius: 0.3rem;
}
.main-content pre {
  padding: 0.8rem;
  margin-top: 0;
  margin-bottom: 1rem;
  font: 1rem Consolas, "Liberation Mono", Menlo, Courier, monospace;
  color: #567482;
  word-wrap: normal;
  background-color: #f3f6fa;
  border: solid 1px #dce6f0;
  border-radius: 0.3rem;
}
.main-content pre > code {
  padding: 0;
  margin: 0;
  font-size: 0.9rem;
  color: #567482;
  word-break: normal;
  white-space: pre;
  background: transparent;
  border: 0;
}
.main-content .highlight {
  margin-bottom: 1rem;
}
.main-content .highlight pre {
  margin-bottom: 0;
  word-break: normal;
}
.main-content .highlight pre, .main-content pre {
  padding: 0.8rem;
  overflow: auto;
  font-size: 0.9rem;
  line-height: 1.45;
  border-radius: 0.3rem;
}
.main-content pre code, .main-content pre tt {
  display: inline;
  max-width: initial;
  padding: 0;
  margin: 0;
  overflow: initial;
  line-height: inherit;
  word-wrap: normal;
  background-color: transparent;
  border: 0;
}
.main-content pre code:before, .main-content pre code:after, .main-content pre tt:before, .main-content pre tt:after {
  content: normal;
}
.main-content ul, .main-content ol {
  margin-top: 0;
}
.main-content blockquote {
  padding: 0 1rem;
  margin-left: 0;
  color: #5F6B70;
  border-left: 0.3rem solid #dce6f0;
}
.main-content blockquote >:first-child {
  margin-top: 0;
}
.main-content blockquote >:last-child {
  margin-bottom: 0;
}
.main-content table {
  display: block;
  width: 100%;
  overflow: auto;
  word-break: normal;
  word-break: keep-all;
}
.main-content table th {
  font-weight: bold;
}
.main-content table th, .main-content table td {
  padding: 0.5rem 1rem;
  border: 1px solid #e9ebec;
}
.main-content dl {
  padding: 0;
}
.main-content dl dt {
  padding: 0;
  margin-top: 1rem;
  font-size: 1rem;
  font-weight: bold;
}
.main-content dl dd {
  padding: 0;
  margin-bottom: 1rem;
}
.main-content hr {
  height: 2px;
  padding: 0;
  margin: 1rem 0;
  background-color: #eff0f1;
  border: 0;
}

.site-footer {
  border-top: solid 1px #eff0f1;
  color: #bbb;
  margin-top: 2rem;
  padding-top: 2rem;
  text-align: right;
}
.site-footer a:link,
.site-footer a:visited {
  color: #999;
}
.site-footer a:hover,
.site-footer a:active {
  color: #333;
  text-decoration: none;
}
@media screen and (min-width:42em) {
  .site-footer {
    font-size: 0.75rem;
  }
}
@media screen and (max-width:42em) {
  .site-footer {
    color: #E5E5E5;
    font-size: 0.8rem;
  }
  .site-footer a:link,
  .site-footer a:visited {
    color: #CDCDCD;
  }
}

ul.post-index {
  margin: 0 auto;
  width: 80%;
}
ul.post-index li {
  padding-left: 6px;
}

.post-meta {
  padding-bottom: 0.5rem;
  opacity: 0.8;
  text-align: right;
}

.post-title {
  font-size: 1.85rem;

  @media screen and (max-width:50em) {
    font-size: 1.15rem;
  }
}
.post-title a:link,
.post-title a:visited {
  color: #5F7585;
}
.post-title a:hover,
.post-title a:active {
  color: #5F634F;
}
.post-header .post-link {
  text-decoration: none;
}
.post-img:before {
  clear: both;
}
.post-img.img-center {
  text-align: center;
}
@media screen and (min-width:50em) {
  .post-img.img-breakout {
    margin-left: -25%;
  }
  .post-img.img-breakout img {
    max-width: none;
  }
  .post-img.img-right {
    float: right;
    margin: 1rem 0 0 1.5rem;
  }
}

.pagination {
  text-align: center;
}

label:hover {
  cursor: pointer;
}
